.headerWrapper {
  max-width: 1100px;
  margin: 0 auto;
}

.table {
  display: flex;
  align-items: center;

  max-width: 1100px;
  margin: 0 auto;
}
.pagination {
  display: flex;
  justify-content: center;

  margin: 20px 0px;
}

.moreIcon{
  cursor: pointer !important;

  margin-left: 10px ;

  border: 2px solid black !important;
  border-radius: 50% !important;
}
.packName {
  display: flex;

  margin: 30px auto;

  font-size: 20px;
  font-weight: bold;
}
.div {
  margin: 40px auto;

  text-align: center;
}
.span {
  opacity: 50%;
}
.icon{
  display:flex;
  align-items: center;
}
.button {
  text-decoration: none;

  background: none;
  border: none;
}
.superButton{
  text-decoration: none;

  background: none;
  border: none;
}
.superButton:hover {
  box-shadow: none;

}
.popover {
  display: flex;
  flex-direction: column;
align-items: start;
}

@media only screen and (max-width: 890px) {
  .tableHead > th {
    max-width: 30px;
    padding: 5px;
  }
  .tableRow > td {
    padding: 0;
  }
  .nameColumn {
    overflow: hidden;

    max-width: 120px;

    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

@media only screen and (max-width: 707px) {
  .lastUpdated {
    display: none !important;
  }
}

.deckCoverBlock {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  align-items: center;

  height: 40px;
}

.deckCoverWrapper {
  position: relative;

  width: 100%;
  max-width: 300px;
  height: 40px;
}

.packDeckCover {
  width: 170px;
  height: 107px;
  margin: 30px auto;

  object-fit: cover;
}